    overflow: hidden;
    /* для установки '…' в абсолютную позицию */
    position: relative;
    /* max-height = line-height (1.2) * число строк (3) */
    max-height: 5.5em;
    /* исправлиление проблемы когда последнее слово не соединяется с правой стороны */
    text-align: justify;
